@import "../../assets/styles/colors";
.auto-complete-input-wrapper {
	position: relative;
}

.search-result-container {
	background: $blue-secondary-overlay;
	z-index: 2;
	top: 2.5rem;
	position: absolute;
	width: 100%;
	overflow: hidden;
	border-radius: 2px 2px 7px 7px;
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.493);
	box-sizing: border-box;
	max-height: 9rem;
	&.overflow-scroll {
		overflow-y: scroll;

		&::-webkit-scrollbar {
			width: 0.5em;
		}

		&::-webkit-scrollbar-track {
			background: darken($blue-primary-overlay, 5%);
		}

		&::-webkit-scrollbar-thumb {
			background-color: darken($gray-1, 5%);
			border-radius: 5px;
		}
	}
	.search-result-item {
		box-sizing: border-box;
		padding: 4px;
		margin-bottom: 2px;
		cursor: pointer;
		font-size: 1rem;
		transition: all 0.2s ease-in-out;
		display: flex;
		flex-direction: row;
		align-items: center;
		&:hover {
			background: $blue-primary-overlay;
			color: $blue-accent;
		}
	}
}

.auto-complete-input {
	z-index: 3;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-content: center;
	position: relative;
	background: $white-1;
	border: 1px solid darken($white-1, 20%);
	padding: 5px;
	border-radius: 5px;

	.search-icon {
		cursor: pointer;
		color: darken($white-1, 20%);
	}
	.clear-icon {
		cursor: pointer;
		transition: 0.2s all ease-in-out;
		&:hover {
			color: darken($white-1, 40%);
		}
	}
	.search-input {
		background: transparent;
		border: none;
		flex-grow: 1;
		outline: none;
		font-size: 1rem;

		&::placeholder {
			color: darken($white-1, 25%);
		}
	}
}

.theme-dark {
	.search-result-container {
		background: $dark-1;
		&.overflow-scroll {
			&::-webkit-scrollbar-track {
				background: darken($dark-1, 5%);
				border-radius: 5px;
			}
			&::-webkit-scrollbar-thumb {
				background-color: darken($dark-3, 5%);
			}
		}
		.search-result-item {
      color: white;
      &:hover {
        background: $dark-2;
        color: $blue-accent-dark;
      }
    }
  }
  .auto-complete-input {
    background: $dark-1;
    border-color: $dark-3;
    .search-input {
      color: white;
    }
  }
}
